<template>
  <v-data-table
    disable-pagination
    disable-filtering
    dense
    hide-default-footer
    :items="configurations"
    :headers="headers"
    :loading="loading"
    loading-text="Carregando"
    no-data-text="Nenhuma configuração encontrada"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          dark
          class="mb-2 ml-2"
          @click="getDefaultConfigs"
        >
          Restaurar para o padrão
        </v-btn>
        <v-btn color="primary" dark class="mb-2 ml-2" @click="save">
          Salvar
        </v-btn>
      </v-toolbar>
      <v-dialog v-model="dialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="text-h5">Editar configuração</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-form ref="form" @submit.prevent>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-select
                      v-model="editedItem.obrigatorio"
                      label="Obrigatório"
                      :items="yesNo"
                      :rules="[rules.required]"
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      v-model="editedItem.chave_xml"
                      label="Chave"
                      :rules="[rules.required]"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      number
                      v-model="editedItem.valor"
                      label="Valor"
                      :rules="[rules.required, rules.greaterOrEqualThanZero]"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close"> Cancelar </v-btn>
            <v-btn color="blue darken-1" text @click="saveItem"> OK </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>

    <template v-slot:[`item.obrigatorio`]="props">
      {{ props.item.obrigatorio ? "Sim" : "Não" }}
    </template>

    <!-- eslint-disable-next-line vue/valid-v-slot -->
    <template v-slot:item.actions="props">
      <v-icon small class="mr-2" @click="editItem(props.item)">
        mdi-pencil
      </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import helper from "@/mixins/helper";
import validationMixin from "@/mixins/validation";

export default {
  name: "ConfiguracoesGeraacao",
  props: ["idConfiguracao"],
  mixins: [helper, validationMixin],
  data() {
    return {
      headers: [
        { text: "Chave", value: "chave_xml" },
        { text: "Métrica associada", value: "desc_metrica" },
        { text: "Valor", value: "valor" },
        { text: "Obrigatório", value: "obrigatorio" },
        { text: "Ações", value: "actions" },
      ],
      configurations: [],
      editedItem: {
        chave_xml: null,
        valor: 0,
        tipo: "setting",
        obrigatorio: 0,
      },
      defaultItem: {
        chave_xml: null,
        valor: 0,
        tipo: "setting",
        obrigatorio: 0,
      },
      editedIndex: -1,
      dialog: false,
      yesNo: [
        { text: "Sim", value: 1 },
        { text: "Não", value: 0 },
      ],
      loading: false,
    };
  },
  mounted() {
    this.load();
  },
  methods: {
    async load() {
      try {
        this.loading = true;
        const resp = await this.$axios.get("/configuracoes_geracao", {
          params: { id_configuracao: this.idConfiguracao },
        });
        this.configurations = resp.data;
      } catch (e) {
        console.error(e);
      } finally {
        this.loading = false;
      }
    },
    async save() {
      const resp = await this.$axios.post("/configuracoes_geracao", {
        id_configuracao: this.idConfiguracao,
        configuracoes: this.configurations,
      });

      if (resp.status === 200) {
        this.$emit("close-dialog");
        this.load();
      }
    },
    async getDefaultConfigs() {
      const resp = await this.$axios.post(
        "/command/clonar_configuracoes_padrao",
        {
          id_configuracao: this.idConfiguracao,
        }
      );
      if (resp.status === 200) {
        this.load();
      }
    },
    editItem(item) {
      this.editedIndex = this.configurations.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    saveItem() {
      if (!this.validate()) {
        return;
      }

      if (this.editedIndex > -1) {
        Object.assign(this.configurations[this.editedIndex], this.editedItem);
      } else {
        this.configurations.push(this.editedItem);
      }
      this.close();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },
};
</script>

